</template>

<script>
	import calendar from '../../components/calendar/calendar.vue';
	export default {
		components: {
			calendar
		},
		data() {
			return {
				// 标头时间实时更新数据
				currentTime: new Date(),
				timer: null,
				// 假数据
				days: [{
						date: '5',
						day: '周四'
					},
					{
						date: '6',
						day: '周五'
					},
					{
						date: '7',
						day: '周六'
					},
					{
						date: '8',
						day: '周日'
					},
					{
						date: '9',
						day: '周一'
					},
					{
						date: '10',
						day: '周二'
					},
					{
						date: '11',
						day: '周三'
					}
				],
				stats: [{
						count: '5',
						label: '今日待办'
					},
					{
						count: '3',
						label: '已完成'
					},
					{
						count: '2',
						label: '重要事件'
					},
					{
						count: '2',
						label: '逾期事件'
					}
				],
				tasks: [{
						title: '完成产品设计方案',
						important: true,
						time: '今天 14:00',
						repeat: '每周一、三、五',
						completed: false,
						status: ''
					},
					{
						title: '健身房锻炼',
						time: '今天 18:30',
						repeat: '每天',
						completed: false,
						status: ''
					},
					{
						title: '购买生日礼物',
						time: '明天 12:00',
						repeat: '',
						completed: false,
						status: ''
					},
					{
						title: '阅读《原子习惯》第三章',
						time: '昨天 20:00',
						repeat: '',
						completed: false,
						status: '逾期'
					},
					{
						title: '回复工作邮件',
						time: '昨天 17:00',
						repeat: '',
						completed: true,
						status: ''
					}
				]
			};
		},
		methods: {
			// 跳转到创建任务页面
			toCreateTask() {
				uni.navigateTo({
					url: '/pages/task/createTask/createTask'
				});
			},
			// 跳转到vip界面
			toVip() {
				uni.navigateTo({
					url: '/pages/toVip/toVip'
				});
			},
		},
		computed: {
			// 标头时间实时更新操作	
			// 当前日期格式化
			currentDate() {
				const year = this.currentTime.getFullYear();
				const month = this.currentTime.getMonth() + 1;
				const date = this.currentTime.getDate();
				return `${year}年${month}月${date}日`;
			},
			// 当前星期
			currentDay() {
				const days = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
				return days[this.currentTime.getDay()];
			},
			// 动态问候语
			greeting() {
				const hours = this.currentTime.getHours();
				if (hours >= 9 && hours < 11) {
					return '上午好';
				} else if (hours >= 11 && hours < 14) {
					return '中午好';
				} else if (hours >= 14 && hours < 19) {
					return '下午好';
				} else if (hours >= 19 && hours < 23) {
					return '晚上好';
				} else {
					return '早上好';
				}
			},
			mounted() {
				// 每分钟更新一次时间
				this.timer = setInterval(() => {
					this.currentTime = new Date();
				}, 60000);
			},
			beforeDestroy() {
				// 组件销毁前清除定时器
				if (this.timer) {
					clearInterval(this.timer);
				}
			}
			//
		},
	};
</script>

